const uni = require('uni').default;

































export default {
	data(){
		return{
			tabbarCurrent:0
		}
	},
	methods:{
		switchTabbarEvent(tabbarIndex){
			this.tabbarCurrent = tabbarIndex;
		},
		changeTabbarEvent(event){
			this.tabbarCurrent = event.detail.current;
		},
		navigateToCategory(){
			uni.switchTab({
				url:'../category/category',
			})
		}
	},
}



